body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px;
  height: 140px;
  background-image: -webkit-linear-gradient(left, #0f6f99 , #0e996a);
  background-image: linear-gradient(to right, #0f6f99 , #0e996a);
}

.header p {
  font-size: 50px;
  font-weight: 500;
  color: white;
  margin: 0px;
}
.body {
  padding: 14px 0 0 0;
}

.favorites {
  background-color: #fafafa;
  text-align: left;
}

.no-results {
  font-size: 18px;
  color: #a1a1a1;
}

.fav-title {
  font-size: 40px;
  font-weight: 620;
  color: #008e40;
  margin: 0 0 14px 14px;
}
.inputs {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly
}

.searchbar {
  padding: 0;
  margin: 0 0 18px;
}

.text-input {
  width: 90vw;
  padding: 14px;
  margin: 0 0 20px;
  border-radius: 2px;
  border-width: 1px;
  border-style: solid;
  border-color: lightgrey;
  font-size: 18px;
}

.search-button {
  width: 35px;
  height: 35px;
  padding: 8px;
  background-color: green;
  border-style: none;
  border-radius: 4px;
}

@media only screen and (max-width: 1000px) {
  .text-input {
    width: 85vw;
  }
}

@media only screen and (max-width: 500px) {
  .text-input {
    width: 70vw;
  }
}
.search-item, .fav-item {
  display: flex;
  flex-direction: row;
  padding: 0 0 18px 20px;
}

.title {
  flex: 2 1;
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  text-align: left;
}

.star {
  height: 25px;
  cursor: pointer;
}

.title p {
  font-size: 18px;
  margin: 0 0 0 10px;
}

button {
  height: 30px;
  width: 30px;
}

.desc {
  flex: 3 1;
  margin: 0 30px 0 20px;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;
}

ul {
  text-align: left;
  margin: 0;
  padding: 0;
}

li {
  font-size: 16px;
  margin: 0 0 14px 6px;
}
